.app.dark {
    background-color: #222;
    color: rgb(156, 156, 156);

    .chartGrid {
        stroke: rgba(228, 228, 228, 0.219)
    }

    .table {
        background-color: #111;

        .tableCell{
            color: gray;
        }
    }

    .navbar{
        color: rgba(153, 153, 153, 0.479);
        border-color: #333;

        .search{
            border-color: gray;
        }
        .wrapper .clickable {
            color: rgb(255, 255, 255);
        }
    }

    .sidebar{
        background-color: #111;
        border-color: #333;

        .top .logo{
            color: #999;
        }

        .center li .inactive {
    
            color: rgba(153, 153, 153, 0.5);
        }

        hr{
            border-color: #333;
        }
        ul li {
            &:hover{
                background-color: #333;
            }
            .icons {
                color: #999;
            }
        }
        
    }

    .datatable {
        .datagrid {
            color: gray;
            border: none;

            .viewButton, .deleteButton{
                color: gray;
                border: none;
            }
            
        }
    }

    input {
        background-color: transparent;
    }
}

