.datatable {
    height: 600px;
    padding: 20px;
}
.datatable .cellWithImg {
    display: flex;
    align-items: center;
}
.datatable .cellWithImg .cellImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
}

.datatable .cellWithStatus {
    padding: 5px;
    border-radius: 5px;

    &.active {
        color: green;
        background-color: rgba(0, 128, 0, 0.08);
    }
    &.pending {
        color: goldenrod;
        background-color: rgba(189, 189, 3, 0.1);
    }
    &.passive {
        color: crimson;
        background-color: rgba(255, 0, 0, 0.05);
    }
}

.datatable .cellAction {
    display: flex;
    align-items: center;
    gap: 15px;
}

.datatable .cellAction .viewButton {
    padding: 2px 5px;
    border-radius: 5px;
    color: darkblue;
    border: 1px dotted rgba(0, 0, 139, 0.596) ;
    cursor: pointer;
}

.datatable .cellAction .deleteButton {
    padding: 2px 5px;
    border-radius: 5px;
    color: crimson;
    border: 1px dotted crimson ;
    cursor: pointer;
}
.datatable .datatableTitle {
    width: 100%;
    font-size: 24px;
    color: grey;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.datatable .datatableTitle .link {
    text-decoration: none;
    color: green;
    font-size: 16px;
    font-weight: 400;
    border: 1px solid green;
    padding: 5px;
    border-radius: 5px;
}