.navbar {
    height: 50px;
    border-bottom: 0.5px solid rgb(231, 228, 228);
    display: flex;
    align-items: center;
    font-size: 14px;
    color: rgba(85, 85, 85, 0.5);
}

.navbar .wrapper {
    width: 100%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar .wrapper .search {
    display: flex;
    align-items: center;
    border: 0.5px solid rgba(211, 211, 211, 0.5);
    padding: 3px;
}
.navbar .wrapper .search input {
    border: none;
    outline: none;
    background-color: transparent;
}
.navbar .wrapper .search input::placeholder {
    font-size: 14px;
}
.navbar .wrapper .items {
    display: flex;
}
.navbar .wrapper .item {
    display: flex;
    align-items: center;
    margin-right: 20px;
    position: relative;

}
.navbar .wrapper .clickable {
    cursor: pointer;
    color: rgb(0, 0, 0);
}
.navbar .wrapper .item .avatar {
    width: 30px;
    height: 30px;
    border-radius: 50%;
}
.navbar .wrapper .item .counter {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: rgba(255, 0, 0, 0.5);
    position: absolute;
    top: -3px;
    right: -6px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 11px;
    font-weight: bold;
}
