.widget {
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    border-radius: 10px;
    height: 100px;
}
.widget .right, .widget .left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.widget .left .title {
    font-weight: bold;
    font-size: 14px;
    color: rgb(160, 160, 160);
}

.widget .left .counter {
    font-weight: 300;
    font-size: 28px;
}

.widget .left .link {
    width: max-content;
    border-bottom: 1px solid gray;
    font-size: 13px;
}

.widget .right .percentage {
    display: flex;
    align-items: center;
    font-size: 16px;
    gap: 4px;

    &.positive{
        color: green;
    }
    &.negative{
        color: red;
    }
}

.widget .right .icon {
    font-size: 24px;
    padding: 5px;
    border-radius: 5px;
    align-self: flex-end;
}