.login {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;   
}

.login form {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login form input {
    width: 220px;
    height: 35px;
    margin: 10px;
}
.login form button {
    width: 200px;
    height: 35px;
    border: none;
    background-color: purple;
    color: white;
    font-weight: bold;
    cursor: pointer;
}
.login form span {
    font-size: 13px;
    color: red;
    margin-top: 10px;
}