.sidebar {
    flex: 1;
    border-right: 0.5px solid rgb(230, 227, 227);
    min-height: 100vh;
    background-color: white;
}
.sidebar .top {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.sidebar .logo {
    font-size: 20px;
    font-weight: bold;
    color: #6439ff;
}

.sidebar hr {
    height: 0;
    border: 0.5px solid rgb(230, 227, 227);
}

.sidebar .center {
    padding-left: 10px;
}

.sidebar .center ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
.sidebar .center li {
    display: flex;
    align-items: center;
    padding: 5px;
    cursor: pointer; 
}
.sidebar .center li:hover {
    background-color: #ece8ff;
}

.sidebar .center li .icons {
    font-size: 22px;
}
.sidebar .center li .active {
    
    color: #7451f8;
}
.sidebar .center li .inactive {
    
    color: #7551f880;
}
.sidebar .center li span {
    font-size: 17px;
    font-weight: 600;
    color: #888;
    margin-left: 10px;
}
.sidebar .center li .textInactive{
    color: rgba(136, 136, 136, 0.582);
}

.sidebar .center .title {
    font-size: 13px;
    font-weight: bold;
    color: #999;
    margin-top: 15px;
    margin-bottom: 5px;
}

.sidebar .bottom {
    display: flex;
    align-items: center;
    margin: 10px;
}

.sidebar .bottom .colorOption{
    width: 20px;
    height: 20px;
    border-radius: 5px;
    border: 1px solid #7451f8;
    cursor: pointer;
    margin: 3px;
}
.sidebar .bottom .colorOption:nth-child(1){
    background-color: whitesmoke;
}
.sidebar .bottom .colorOption:nth-child(2){
    background-color: #333;
}
