.chart {
    flex: 4;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    color: gray;
    padding: 10px;
}

.chart .title {
    display: flex;
    margin-bottom: 10px;
    justify-content: center;
    font-size: 22px;
    font-weight: 500;
}
.chart .chartGrid {
    stroke: rgb(228, 225, 225);
}